import React from 'react'
import './Minibrowser.css'

const Minibrowser = props => {
	const { 
		srcDoc, 
		lessonName, 
		handleRunMinibrowser, 
		sendRunMinibrowser 
	} = props
	
	const handleClick = () => {
		handleRunMinibrowser()
		sendRunMinibrowser()
	}
	
	return (
		<div className="Minibrowser Flex Column SpaceBetween">
			<div 
				className="Flex SpaceBetween AlignCenterContent"
				id="BrowserHeader"
			>	
				<button 
					className='NeuBtn IconBtn'
					onClick={ handleClick }
					id="RunBtn"
				>
					<div className='iconContainer'>
						<div 
							className='Run'>
						</div>
					</div>
				</button>
				<div 
					className="Flex AlignCenterContent"
					id="LessonName"
				>
					<p className="text">Lesson :</p>
					<input 
						className="Input"
						readOnly 
						value={ lessonName } 
					/>
				</div>
			</div>
			<div 
				className="Card"
				id="IframeContainer"
			>			
				<iframe 
					srcDoc={ srcDoc }
					title="output"
					sandbox="allow-scripts"
					frameBorder="0"
				/>
			</div>
		</div>
	)
}

export default Minibrowser
